import React, { Component } from 'react'
import {connect} from 'react-redux'//引入redux容器组件
import { CITY } from '@/redux/city_location/variable.js'//引入reducers变量
import { SWITCH } from '@/redux/switch_state/variable.js'
import { RECOMMENDLINE } from '@/redux/recommend/variable.js'
import { nearSites , recommendLine , selectLineCars} from '@/tool/api/real_bus.js'
import { _setRecommend , _sortView , _mergeList,_setFlag,_filter} from '@/tool/Processing.js'
import { _getLocationAMap} from '@/tool/location.js'
import Loading from '@/tool/loading/Loading.js'
import down from '@/assets/imgs/down.png'
import Searchbar from '@/components/searchBar/index'
import Platform from '@/components/platform/index'
import Select from '@/components/select_components/index'
import Buscard from '@/components/bus_card/index'
import './index.scss'
class Index extends Component {
    state={
        nearSitesBus:{},
        mergeList:[],
        flagas:1,
        time:null,
        timer:null,
        is_toast:true
    }
    _getNearSites = async () =>{//附近站点信息
        const {userId,city,country,latitude,longitude,province} = this.props.mylocation
        console.log(latitude,longitude,'latitude,longitude');
        const { code , data } = await nearSites({userId,city,country,latitude,longitude,province})
        if( code!==200) return
        this.setState({
            nearSitesBus:data
        })
    }
    _getRecommendLine = async () =>{//站点推荐列表
        const {userId,city,country,latitude,longitude,province} = this.props.mylocation
        const {code,data:realBusData} = await recommendLine({range:1000,userId,city,country,latitude,longitude,province})
        if( code!==200) return
        realBusData.forEach(item=>{
            item.soleId=item.lineId+item.upDown
            item.ranNum=Math.random(1)
            item.flag=0
        })
        this._getSelectLineCars(realBusData)
    }
    _getSelectLineCars = async (array) =>{//实时公交
        let recommendList
        const { flagas ,is_toast} = this.state
        const { userId } = this.props.mylocation
        let recommendLineBus=array
        let operation
        let cache
        if(is_toast)  Loading.show()
        if(flagas===1){
            cache=JSON.parse(localStorage.getItem('collection'))||[]
            operation=_setFlag(recommendLineBus,cache)
            recommendList = _setRecommend( operation )
        }
        if(flagas===3){
            cache=JSON.parse(localStorage.getItem('collection'))||[]
            operation=_filter(recommendLineBus,cache)
            recommendList = _setRecommend( operation )
        }
        if(flagas===2){
            cache=JSON.parse(localStorage.getItem('historyList'))||[]
            operation=_filter(recommendLineBus,cache)
            recommendList = _setRecommend( operation )
        }
        const { data:selectLineCarsList,code } = await selectLineCars({userId,lineSiteCarsParams:recommendList})
        if( code!==200) return
        if(is_toast)  Loading.remove()
        let mergeData = _mergeList(_sortView(selectLineCarsList),_sortView(operation))//合并数据
        this.props._changerecommend(mergeData)//将数据传到公共状态中
        this.state.is_toast=false
    }
    _setPage = (index) =>{//改变组件
        this.props._changeSwitch(index)
        this.props._changerecommend([])
        this.setState({flagas:index},()=>{
            this._getRecommendLine()
        })
    }
    _refresh = () =>{
        // 刷新
        this._getRecommendLine()
    }
    _getUrl(url){
        let obj = {}
        let params =url?url.split("?")[1].split("&"):[]
        params.map(item => obj[item.split("=")[0]] = item.split("=")[1])
        return obj;
    }
    componentDidMount(){
       const { mylocation } =this.props;
       let url=this.props.location.search
       let viewData = this._getUrl(url)
       if(Object.keys( viewData ).length){//地址栏有IP地址时
            const { lat ,lng} = viewData;
            mylocation['latitude']=lat;
            mylocation['longitude']=lng;
            this.props._changeCity(mylocation)
            this._getNearSites()
            this._getRecommendLine()
            this.state.timer=setInterval(()=>{
                this._getRecommendLine()
            },15000)
       }else{//地址栏没有地址时
            Loading.show()
            _getLocationAMap((e)=>{
                const { latitude ,longitude,province,district} = e;
                mylocation['latitude']=latitude;
                mylocation['longitude']=longitude;
                mylocation['province']=province;
                mylocation['country']=district;
                this.props._changeCity(mylocation)
                this._getNearSites()
                this._getRecommendLine()
                Loading.remove()
                this.state.timer=setInterval(()=>{
                    this._getRecommendLine()
                },15000)
            })
       }
    }
    componentWillUnmount(){
        // clearInterval(this.state.timer&&this.state.timer)
        this.setState = (state,callback)=>{
            return;
        };
    }
    render() {
        const { mylocation } = this.props
        const { nearSitesBus,flagas } = this.state
        const ISNULL=nearSitesBus?nearSitesBus:{}
        const is_null = Object.keys( ISNULL ).length
        // console.log(this.props.location.search)
        return (
            <div className="index"> 
                <div className="index_top">
                    <div className="text" onClick={()=>{this.props.history.push({pathname:'/location',state:{log:mylocation.longitude,lat:mylocation.latitude}})}}>
                         <span>{mylocation.country}</span>
                         <img src={ down } alt=""/>
                    </div>
                </div>
                <Searchbar source={ true }></Searchbar>
                <Platform states={ is_null } platData={ nearSitesBus }></Platform>
                {/* 主体内容 */}
                <div className="index_main">
                    <Select flag={flagas} pageIndex={this._setPage} refresh={this._refresh}></Select>
                    <Buscard></Buscard>
                </div>
            </div>
        )
    }
}

const mapStateToProps=(state)=>({mylocation:state.locationCity,myLineData:state.recommendData})
const mapDisPatchToProps=(dispatch)=>({
    _changeCity:(number)=>{
        dispatch({type:CITY,data:number})
    },
    _changerecommend:( busReal ) =>{
        dispatch({type:RECOMMENDLINE,data:busReal })
    },
    _changeSwitch:(flagId)=>{
        dispatch({type:SWITCH,data:flagId })
    }
})


export default connect(mapStateToProps,mapDisPatchToProps)( Index )