import React, { Component , Fragment} from 'react'
import {connect} from 'react-redux'//引入redux容器组件
import { withRouter } from 'react-router-dom'
import { RECOMMENDLINE } from '@/redux/recommend/variable.js'
import Bulletframe from '@/components/Bulletframe/index'
import Cardchildred from '@/components/card_children/index'
import failing from '@/assets/imgs/failing.png'
import fav2 from '@/assets/imgs/fav2.png'
import down2 from '@/assets/imgs/down2.png'
import push from '@/assets/imgs/push.png'
import home from '@/assets/imgs/home.png'
import company from '@/assets/imgs/company.png'
import fav from '@/assets/imgs/fav.png'
import './index.scss'
class Buscard extends Component {
    state={
        dropdown:false,//下拉
        frame:false,//模态框
        completeData:[],
        soleId:0//控制收藏的位于id
    }
    _setImgDOM = (index) =>{
        if(index===1) return (<img src={home} alt="" />)
        if(index===2) return (<img src={company} alt="" />)
        if(index===3) return (<img src={fav} alt="" />)
    }
    _changeFlag = (id) =>{
        const { frame } = this.state
        this.setState({
            frame:!frame,
            soleId:id
        })
    }
    _dropDown = () =>{
        const { dropdown } = this.state
        const { myLineData } =this.props
        let dataSlice= myLineData.slice(0,3)
        let updateData
        if(!dropdown){
            updateData=myLineData
        }else{
            updateData=dataSlice
        }
        this.setState({
            dropdown:!dropdown,
            completeData:updateData
        })
    }
    _changeFrame = (e) =>{
        this.setState({
            frame:e
        })
    }
    _changePage = (lineId)=>{
        this.props.history.push({pathname:'/details',state:{lineId}})
    }
    render() {
        const { myLineData } = this.props
        const { dropdown,frame,soleId } = this.state
        let realBusSlice=myLineData.slice(0,3)
        let states=myLineData.length
        let viewList= dropdown?myLineData:realBusSlice
        return (
           <Fragment>
              {
                  states
                  ?<div className="real">
                      <div className="real_top">
                          <div className="top_left"></div>
                          <div className="top_right">实时公交</div>
                      </div>
                      <div className="real_main">
                          {
                              viewList.map((item)=>{
                                  return (
                                      <div className="main_box" key={item.ranNum}>
                                          <div className="box_left">
                                            <div>
                                                <span>线路</span>
                                                <span className="left_span">{item.lineName}</span>
                                            </div>
                                            <div className="left_bottom">
                                                <span>开往</span>
                                                <span className="left_span">{item.endSite}</span>
                                            </div>
                                            <div>
                                                <span>侯站</span>
                                                <span className="left_span">{item.siteName}</span>
                                            </div>
                                          </div>
                                          <div className="box_right">
                                              <Cardchildred status={item.runStauts} siteOrder={item.siteOrder} list={item.list} lineId={item.lineId}></Cardchildred>
                                              <div className="right_line"></div>
                                              <div className="right_img" onClick={()=>this._changeFlag(item.soleId)}>
                                                  {
                                                      item.flag
                                                      ?this._setImgDOM(item.flag)
                                                      :<img src={fav2} alt="" />
                                                  }
                                              </div>
                                          </div>
                                      </div>
                                  )
                              })
                          }
                      </div>
                      <div className="real_fl" onClick={this._dropDown}>
                          {
                              dropdown
                              ?<img src={push} alt="" />
                              :<img src={down2} alt="" />
                          }
                      </div>
                      { frame?<Bulletframe soleId={soleId} changeFeame={this._changeFrame}></Bulletframe>:'' }
                  </div>
                  :<div className="remom_null">
                        <div className="null_cen">
                            <img src={failing} alt="" />
                            <div>暂无线路信息</div>
                        </div>
                    </div>
              } 
           </Fragment>
        )
    }
}
const mapStateToProps=(state)=>({myLineData:state.recommendData})
const mapDisPatchToProps=(dispatch)=>({
    _changerecommend:( busReal ) =>{
        dispatch({type:RECOMMENDLINE,data:busReal })
    }
})


export default connect(mapStateToProps,mapDisPatchToProps)( withRouter(Buscard) )

