import React, { Component , Fragment} from 'react'
import {connect} from 'react-redux'//引入redux容器组件
import { RECOMMENDLINE } from '@/redux/recommend/variable.js'
import { SWITCH } from '@/redux/switch_state/variable.js'
import {his_coll} from '@/tool/location.js'
import ref from '@/assets/imgs/ref.png'
import './index.scss'
class Select extends Component {
    constructor(props){
        super(props)
        this.state={
            flag:1
        }
    }
    _changState = (index) =>{
        this.props.pageIndex(index)
        this.props._changerecommend([])
        this.setState({
            flag:index
        })
    }
    _setClass = (flagId) =>{
        if(flagId===1) return 'changeColorbig'
        if(flagId!==1) return 'changeColor'
    }
    _setRefresh = () =>{
        this.props.refresh()
    }
    render() {
        const { flag } = this.state
        return (
            <Fragment>
                <div className="select">
                    <div className="select_list">
                        {
                            his_coll.map(item=>{
                                return (
                                    <div className={flag===item.flag?this._setClass(item.flag):'list_child'}
                                    onClick={()=>this._changState(item.flag)}
                                     key={item.id}
                                     >{item.title}</div>
                                )
                            })
                        }
                    </div>
                    <div className="select_right" onClick={this._setRefresh}>
                        <img src={ref} alt="" />
                        <span>刷新</span>
                    </div>
                </div>
            </Fragment>
        )
    }
}
const mapDisPatchToProps=(dispatch)=>({
    _changerecommend:( busReal ) =>{
        dispatch({type:RECOMMENDLINE,data:busReal })
    },
    _changeSwitch:(id)=>{
        dispatch({type:SWITCH,data:id })
    }
})


export default connect((state)=>({
    myLineData:state.recommendData,
    switchID:state.switchState
}),mapDisPatchToProps)(Select)

