import React, { Component ,Fragment} from 'react'
import { withRouter } from 'react-router-dom'
import failing from '@/assets/imgs/failing.png'
import './index.scss'
class Platform extends Component {
    render() {
        const { states , platData } = this.props
        return (
           <Fragment>
               {
                   states
                   ?<div className="index_remom" onClick={()=>this.props.history.push({pathname:'/platform',state:{name:platData.name}})}>
                        <div className="remom_top navigator_arrow">{platData.name}</div>
                        <div className="remom_text">
                            <div className="text_left">公交</div>
                            <div className="text_right">
                                {
                                    platData.lineSites.map(item=>{
                                        return (
                                            `${item.lineName}\xa0`
                                        )
                                    })
                                }
                            </div>
                        </div>
                    </div>
                    :<div className="remom_null">
                        <div className="null_cen">
                            <img src={failing} alt="" />
                            <div>该站点暂无线路信息</div>
                        </div>
                    </div>
               }
           </Fragment>
        )
    }
}

export default withRouter( Platform )